<template>
	<div id="cart">
		<nav-bar>
			<template #center>
				<h2>购物车({{cartMount}})</h2>
			</template>
		</nav-bar>
		<cart-list :cartInfo="cartInfo"></cart-list>
		<bottom-bar></bottom-bar>
	</div>
</template>

<script>
import NavBar from '../../../components/common/navbar/NavBar';
import CartList from './ChildComponent/CartList';
import BottomBar from './ChildComponent/BottomBar';

export default {
	components: {
		NavBar,
		CartList,
		BottomBar,
	},
	data() {
		return {};
	},
	computed: {
		cartMount() {
			return this.$store.state.cartList.length;
		},
		cartInfo() {
			return this.$store.state.cartList;
		},
	},
	created() {},
};
</script>

